/* Design section styling */
.design-section {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 80px 40px;
    gap: 40px;
    max-width: 1200px;
    margin: 0 auto;
    position: relative;
    z-index: 1;
    background-color: #f9f9f9;
    opacity: 0;
    transform: translateY(20px);
    animation: fadeInUp 0.8s ease forwards;
}

@keyframes fadeInUp {
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.text-content {
    flex: 1;
    max-width: 50%;
    opacity: 0;
    transform: translateX(-20px);
    animation: fadeInLeft 0.8s ease forwards 0.3s;
}

@keyframes fadeInLeft {
    to {
        opacity: 1;
        transform: translateX(0);
    }
}

.text-content h2 {
    font-size: 2.5em;
    margin-bottom: 20px;
    color: var(--logo-green);
    position: relative;
    display: inline-block;
}

.text-content h2::after {
    content: '';
    position: absolute;
    bottom: -10px;
    left: 0;
    width: 60px;
    height: 3px;
    background-color: var(--logo-green);
    transition: width 0.3s ease;
}

.text-content:hover h2::after {
    width: 100%;
}

.highlight-text {
    color: var(--logo-green);
    font-weight: bold;
    font-size: 1.2em;
    margin: 20px 0;
    display: inline-block;
    position: relative;
}

.text-content p {
    font-size: 1.1em;
    line-height: 1.8;
    color: var(--grey);
    margin-bottom: 20px;
}

.image-content {
    flex: 1;
    max-width: 50%;
    opacity: 0;
    transform: translateX(20px);
    animation: fadeInRight 0.8s ease forwards 0.3s;
}

@keyframes fadeInRight {
    to {
        opacity: 1;
        transform: translateX(0);
    }
}

.image-content img {
    width: 100%;
    border-radius: 12px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease;
}

.image-content:hover img {
    transform: scale(1.05);
}

/* Services section */ 
.services {
    padding: 80px 20px;
    background: #ffffff !important;
    position: relative;
    z-index: 1;
}

.services::before,
.services::after {
    display: none;
}

.services h2 {
    text-align: center;
    font-size: 2.5em;
    margin-bottom: 60px;
    color: var(--logo-green);
}

.service-boxes {
    display: flex;
    justify-content: center;
    gap: 30px;
    max-width: 1200px;
    margin: 0 auto;
}

.service-box {
    background-color: var(--logo-green);
    color: var(--white);
    padding: 40px;
    border-radius: 12px;
    width: 45%;
    position: relative;
    transition: all 0.3s ease;
    overflow: hidden;
}

.service-box::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(45deg, transparent, rgba(255, 255, 255, 0.1), transparent);
    transform: translateX(-100%);
    transition: transform 0.6s ease;
}

.service-box:hover::before {
    transform: translateX(100%);
}

.service-box:hover {
    transform: translateY(-10px);
    box-shadow: 0 15px 40px rgba(0, 0, 0, 0.2);
}

.service-box h3 {
    font-size: 1.8em;
    margin-bottom: 20px;
    position: relative;
    display: inline-block;
}

.service-box h3::after {
    content: '';
    position: absolute;
    bottom: -8px;
    left: 0;
    width: 40px;
    height: 2px;
    background-color: var(--white);
    transition: width 0.3s ease;
}

.service-box:hover h3::after {
    width: 100%;
}

.service-box p {
    font-size: 1.1em;
    line-height: 1.6;
    margin-bottom: 25px;
}

.service-box .btn {
    background-color: var(--white);
    color: var(--black);
    padding: 12px 30px;
    border-radius: 25px;
    text-decoration: none;
    display: inline-block;
    transition: all 0.3s ease;
    position: relative;
    overflow: hidden;
}

.service-box .btn:hover {
    background-color: var(--hover-color);
    color: var(--white);
    transform: translateY(-2px);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

.badge {
    position: absolute;
    top: 15px;
    right: 15px;
    background-color: var(--white);
    color: var(--black);
    padding: 8px 15px;
    font-size: 0.9em;
    border-radius: 20px;
    transition: all 0.3s ease;
}

.service-box:hover .badge {
    transform: scale(1.1);
}

/* Slideshow Container */
.slideshow-container {
    width: 100%;
    height: 600px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f0f0f0;
    overflow: hidden;
    position: relative;
}

.slideshow-container img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.5s ease;
}

.slideshow-container:hover img {
    transform: scale(1.05);
}

/* Responsive Design */
@media (max-width: 900px) {
    .design-section {
        padding: 60px 20px;
        flex-direction: column;
        text-align: center;
        gap: 30px;
    }

    .text-content, .image-content {
        max-width: 100%;
    }

    .text-content h2 {
        font-size: 2em;
    }

    .text-content p {
        font-size: 1em;
    }

    .image-content {
        margin-top: 20px;
    }

    .slideshow-container {
        height: 400px;
    }

    .services {
        padding: 60px 20px;
    }

    .services h2 {
        font-size: 2em;
        margin-bottom: 40px;
    }

    .service-boxes {
        flex-direction: column;
        align-items: center;
        gap: 20px;
    }

    .service-box {
        width: 90%;
        padding: 30px;
    }

    .service-box h3 {
        font-size: 1.5em;
    }

    .service-box p {
        font-size: 1em;
    }

    .badge {
        font-size: 0.8em;
        padding: 6px 12px;
    }
}

/* For very small screens */
@media (max-width: 480px) {
    .design-section {
        padding: 40px 15px;
    }

    .text-content h2 {
        font-size: 1.8em;
    }

    .slideshow-container {
        height: 300px;
    }

    .services h2 {
        font-size: 1.8em;
    }

    .service-box {
        width: 95%;
        padding: 25px;
    }

    .service-box h3 {
        font-size: 1.3em;
    }

    .service-box p {
        font-size: 0.9em;
    }

    .btn {
        padding: 10px 25px;
        font-size: 0.9em;
    }
}